body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: lightsteelblue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.factories, .playerBoard{
  display: flex;
  margin-top: 50px;
}

.centerRow{
    margin-top: 20px;
    margin-left: 10px;
    min-width: 1100px;
}

.playerScore{
    font-weight: 700;
    font-size: 40px;
    position: absolute;
    margin-top: 20px;
    left: 50px;
}

.playerLines{
  width: 400px;
  margin-right: 100px;
}

.droppedTilesRow{
  align-self: flex-end;
  top: 120px;
  position: relative;
  margin-left: -820px;
  text-align: center;
  font-weight: 700;
  font-size: larger;
}

.square{
  width: 50px;
  height: 50px;
  border: 1px solid black;
  background-color: beige;
  padding: 10px;
}

.emptyWall{
  opacity: 0.35;
  filter: blur(1px);
}

.filledWall{
    box-shadow: 5px 5px 8px 2px rgb(0 0 0);
}

.tile {
  width: 50px;
  height: 50px;
  position: absolute;
  box-shadow: 5px 5px 8px 2px rgb(0 0 0);
}

.topLeft {
  top: 35px;
  left: 35px;
}

.topRight {
  top: 35px;
  right: 35px;
}

.bottomLeft {
  bottom: 35px;
  left: 35px;
}

.bottomRight {
  bottom: 35px;
  right: 35px;
}

.selectedTile {
  box-shadow: 0 0 20px 5px rgb(255 247 2);
}

.selectedRow {
  box-shadow: 0 0 20px 5px rgb(38 241 229);
}

.clearedTile{
  visibility: hidden;
}

.blueTile{
    background: linear-gradient(63deg, #6666ff 23%, transparent 23%) 7px 0,
    linear-gradient(63deg, transparent 74%, #6666ff 78%),
    linear-gradient(63deg, transparent 34%, #6666ff 38%, #6666ff 58%, transparent 62%),
    #000099;
    background-size: 16px 48px;
}

.darkgreyTile{
    background:
    radial-gradient(circle, transparent 20%, slategray 20%, slategray 80%, transparent 80%, transparent),
    radial-gradient(circle, transparent 20%, slategray 20%, slategray 80%, transparent 80%, transparent) 12.5px 12.5px,
    linear-gradient(#A8B1BB 2px, transparent 2px) 0 -1px,
    linear-gradient(90deg, #A8B1BB 2px, transparent 2px) -1px 0;
    background-color: slategray;
    background-size:25px 25px, 25px 25px, 12.5px 12.5px, 12.5px 12.5px;
}

.redTile{
    
    background: linear-gradient(115deg, 
    transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
    linear-gradient(245deg, transparent 75%,rgba(255,255,255,.8) 75%) 0 0, 
    linear-gradient(115deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px, 
    linear-gradient(245deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px,red;
    background-size: 15px 30px;
}